@import url('https://fonts.googleapis.com/css2?family=Inter:wght@300;400;500;600;700;800&display=swap');

*{
    border:0;
    margin: 0;
    padding: 0;
    outline: 0;
    text-decoration: none;
}

:root {
    --container-lg: 75%;
    --container-md: 85%;
    --container-sm: 92%;
}



section{
    margin:2rem 0;
}

.container {
    width: var(--container-lg);
    margin: 0 auto;
    font-family: 'inter', sans-serif;
}

.roi-calculator__header {
    width: 550px;
    font-size: 24px;
    font-weight: 600;
}

.calculator-form__container {
    display: grid;
    grid-template-columns: 2fr 1fr;
    gap:2rem;
    margin: 2rem 0;    
}

.calculator-form__container .col:nth-child(1) {
    display: grid;
    grid-template-columns: repeat(2,1fr);
    gap:1rem;
}

.calculator-form__container .col:nth-child(1) select {
    border:solid 1px #c7c7c7;
    padding: .5rem;
    font-size: 1rem;
    border-radius: .5rem;
    background: transparent;
}

.calculator-form__container fieldset {
    display: flex;
    flex-direction: column;
    gap:8px;
    justify-content: end;
    padding: 1rem;
}

.calculator-form__container fieldset h4 {
    font-size: 18px;
}

.calculator-form__container fieldset span{
    color: #848484;
    font-size: 16px;
    margin:.5rem 0 1rem;
    max-width: 320px;
}

input[type="range"] {
    -webkit-appearance: none;
    width: 250px;
    margin-right: 15px;
    height: 3px;
    background: #D9D9D9;
    background-image: linear-gradient(#F2BD2A, #F2BD2A);
    background-size: 0% 100%;
    background-repeat: no-repeat;
  }

  /* Input Thumb */
input[type="range"]::-webkit-slider-thumb {
    -webkit-appearance: none;
    height: 12px;
    width: 12px;
    border-radius: 50%;
    background: #F2BD2A;
    cursor: ew-resize;
    box-shadow: 0 0 2px 0 #555;
    transition: background .3s ease-in-out;
  }

  input[type="range"]::-moz-range-thumb {
    -webkit-appearance: none;
    height: 12px;
    width: 12px;
    border-radius: 50%;
    background: #F2BD2A;
    cursor: ew-resize;
    box-shadow: 0 0 2px 0 #555;
    transition: background .3s ease-in-out;
  }

  input[type="range"]::-ms-thumb {
    -webkit-appearance: none;
    height: 12px;
    width: 12px;
    border-radius: 50%;
    background: #F2BD2A;
    cursor: ew-resize;
    box-shadow: 0 0 2px 0 #555;
    transition: background .3s ease-in-out;
  }

  /* Input Track */
input[type=range]::-webkit-slider-runnable-track  {
    -webkit-appearance: none;
    box-shadow: none;
    border: none;
    background: transparent;
  }

  input[type=range]::-moz-range-track {
    -webkit-appearance: none;
    box-shadow: none;
    border: none;
    background: transparent;
  }
  
  input[type="range"]::-ms-track {
    -webkit-appearance: none;
    box-shadow: none;
    border: none;
    background: transparent;
  }

.calculator-form__container .result-box {
    display: flex;
    flex-direction: column;
    gap:.5rem;
    height: 96px;
    justify-content: center;
    align-items: flex-start;
    box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.07);
    border-radius: 7px;
    padding: 1rem .8rem ;
}

.calculator-form__container .col > .result-box {
    margin-top: 2rem;
    align-items:center;
}

.calculator-form__container .col > span {
    font-size: 14px;
    color: #B1B1B1;
    line-height: 1.3;
}

.calculator-form__container .result-box .result {
    font-size: 2rem;
    font-weight: 600;
}

.calculator-form__container .result-row {
    display: grid;
    grid-template-columns: repeat(2,1fr);
    gap:2rem;
    margin:1rem 0;
}

.calculator-form__container .result-row .result-box span {
    font-size: 12px;
    color: #848484
}


.calculator-form__container .result-row .result-box .result, 
.calculator-form__container .result-box .result {
    font-size: 2rem;
    font-weight: 600;
    color:#F2BD2A
}

@media screen and ( max-width: 1024px ) {
    .container {
        width: var(--container-md);
    }

    .calculator-form__container {
        grid-template-columns: 1fr;
    }
}

@media screen and ( max-width: 767px ) {
    .container {
        width: var(--container-sm);
    }

    .roi-calculator__header{
        max-width: 375px;
        font-size: 20px;
    }

    .calculator-form__container .col:nth-child(1) {
        grid-template-columns: 1fr;
    }
}